.wp-block-ecv-gutenberg-custom-media{
  position: relative;
  img{
	width: 100%;
  }
  .custom-text{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: rgba(0,0,0,0.5);
	color: white;
	font-size: 2rem;
	cursor: pointer;
	p{
	  margin: 0;
	  font-size: 16px;
	  padding: 10px 20px;
	}
  }
}
